@import "../../assets/styles/variables";
@import "../../assets/styles/mixins";
@import "../../assets/styles/sprite";

.brk-post-full {
  &__meta {
    display: flex;
    &-date {
      color: #585858;
      margin-right: 15px;
			display: flex;
			align-items: center;
			justify-content: center;

      > i {
        margin-right: 5px;
        font-size: 10px;
      }
    }
    &-comments {
      color: #757575;
			display: flex;
			align-items: center;
			justify-content: center;

      > i {
        margin-right: 5px;
        font-size: 10px;
      }
    }
    &-date,
    &-comments{
      i{
        vertical-align: middle;
      }
    }
  }
  &__fixed-width {
    min-width: 73px;
  }
  .rotation-slider {
    max-width: none;
  }
}

.brk-related-carousel {
 
  &__control {
    display: flex;
    justify-content: space-between;
    position: relative;
    flex-grow: 1;
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      right: 0;
      height: 1px;
      background-color: #ececec;
    }
    &-btn {
      z-index: 1;
      padding: 15px 64px;
      border: 1px solid #ececec;
      background-color: #ffffff;
      border-radius: 35px;
      display: flex;
      align-items: center;
      text-transform: uppercase;
      transition: .3s ease all;
      .brk-bordered-theme & {
        border-radius: var(--b-radius);
      }
      @media screen and (max-width:500px){
        flex-grow: 1;
        padding: 10px;
        justify-content: center

      }
      &:first-child>i {
        margin-right: 10px;
        margin-top: -4px;
      }
      &:last-child>i {
        margin-left: 10px;
        margin-top: -4px;
        
      }
      &:hover { 
       
        color: #ffffff; 
      }
    }
  }
 
  .slick-list {
    margin: -15px;
  }
  .slick-slide {
    padding: 15px;
  }

  &__dots {
    display: flex;
    justify-content: center;
    margin-top: 50px;
    li {
      width: 26px;
      height: 9px;
      overflow: hidden;
      color: transparent;
      background-color: transparent;
      margin-right: 10px;
      cursor: pointer;
      border-radius: 20px;
      .brk-bordered-theme & {
        border-radius: var(--b-radius);
      }
      &:last-child {
        margin-right: 0;
      }
      button {
        background-color: transparent;
        width: 100%;
        height: 100%;
      }
    }
    
    li {
      border: 1px solid rgba(0, 0, 0, 0.13);
      box-shadow: inset 0px 0px 0px 1px $white;
    }
    .slick-active {
      border: 1px solid rgba(0, 0, 0, 0.13);
    }
    
  }
  
}

.brk-reply-item {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	padding: 40px 0;
	
	&:after {
		content: '';
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		height: 1px;
		background: url(../../img/dashed-border.png) repeat-x;
	}

	&:last-child {
		&:after {
			background: none;
		}
	}

	.brk-reply-item {
		padding-left: 30px;
		border-bottom: none;
		padding-bottom: 0;

		@media (max-width: 767px) {
			padding-left: 30px;
		}

		@media (max-width: 575px) {
			padding-left: 15px;
			padding-top: 20px;
		}

		&:after {
			background: none;
		}
	}

	&__user {
		flex: 0 0 50px;
		max-width: 50px;
		height: 50px;
		position: relative;
		overflow: hidden;
		border-radius: 50%;
		display: block;
		margin-right: 15px;

		@media (max-width: 767px) {
			flex: 0 0 40px;
			max-width: 40px;
			height: 40px;
		}

		img {
			width: 100%;
			height: auto;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
	}

	&__content {
		flex-basis: 0;
		flex-grow: 1;
		max-width: 100%;
	}

	&__header {

		@media (min-width: 576px) {
			order: 1;
			flex: 0 0 auto;
			width: auto;
			max-width: 100%;
		}

		&-like {
			font-size: rem(14);
			color: #9f9f9f;

			> i {
				margin-right: 7px;
				margin-top: 5px;
			}
		}

		&-reply {
			font-size: rem(14);
			margin-left: 15px;

			@media (max-width: 991px) {
				margin-left: 10px;
			}

			@media (max-width: 767px) {
				font-size: rem(13);
				margin-left: 5px;
			}

			> i {
				margin-right: 8px;
				margin-top: 4px;
			}
		}
	}
}